import Grid from './grid.js';
import primitives from './primitives.js';
import { SelectionState, MandalaState } from './state.js';
import * as uiJs from './ui.js';

/* Setup state */

const order = 8;
const parameters = [
  { name: 'distance',     min: '0', max: '1',   step: '0.001', initial: '0.5' },
  { name: 'angle',        min: '-180', max: '180', step: '0.001', initial: '0' },
  { name: 'rotation',     min: '0', max: '360', step: '0.001', initial: '0' },
  { name: 'scale',        min: '0', max: '1',   step: '0.001', initial: '0.1' },
  { name: 'multiplicity', min: '1', max: '24',   step: '1',     initial: '1' }
];
const [ canvasWidth, canvasHeight ] = [ 800, 800 ];
const mainCanvas = document.querySelector('#main-canvas');
[ mainCanvas.width, mainCanvas.height ] = [ canvasWidth, canvasHeight ];

const selectionState = new SelectionState();
const mandalaState   = new MandalaState();

const grid = new Grid();
grid.draw();

/* Image */

/* Clear the canvas */

function clear(canvas) {
  const context = canvas.getContext('2d');
  context.clearRect(0, 0, canvas.width, canvas.height);
}

/* Events */

/* Top-level drawing function */

let tick = 0;

function draw() {
  clear(mainCanvas);

  grid.drawImageTo(mainCanvas, 0, 0);

  mandalaState.drawImageTo(mainCanvas);
}

/* Setup UI when the page has loaded */

window.onload = function () {
  window.requestAnimationFrame(draw);

  uiJs.setupGridToggle((flag) => {
    grid.visible = flag;
    requestAnimationFrame(draw);
  });

  uiJs.setupSaveButton(() => {
    const saveCanvas = document.createElement('canvas');

    saveCanvas.width  = canvasWidth;
    saveCanvas.height = canvasHeight;

    mandalaState.drawImageTo(saveCanvas);

    saveCanvas.toBlob(blob => {
      const downloadLink = document.createElement('a');
      
      downloadLink.href = URL.createObjectURL(blob);
      downloadLink.download = 'mandala.png';
      downloadLink.classList.add('hidden');

      document.body.appendChild(downloadLink);
      downloadLink.click();
      document.body.removeChild(downloadLink);
    });
  })

  for (const id in primitives) {
    function clickHandler() {
      const symbol = Symbol();

      mandalaState.addPrimitive(symbol, primitives[id], { order });
      changeSelection(symbol);

      return symbol;
    }

    function selectHandler(symbol) { changeSelection(symbol); }

    function deleteHandler(symbol) {
      mandalaState.removePrimitive(symbol);
      changeSelection(null);
    }

    uiJs.addPrimitiveButton(primitives[id], clickHandler, selectHandler, deleteHandler);
  }

  parameters.forEach(({ name, min, max, step, initial }) => {
    function inputHandler(value) {
      const props = {};
      props[name] = value;
      
      mandalaState.updatePrimitiveState(selectionState.getState(), props);
      uiJs.updateControlsFor(name, value);
      
      requestAnimationFrame(draw);
    }
  
    uiJs.setupControlsFor(name, { min, max, step, initial }, inputHandler);
  });

  uiJs.setupFlipToggle((flag) => {
    mandalaState.updatePrimitiveState(selectionState.getState(), { flip: flag });
    uiJs.updateFlipToggle(flag);

    requestAnimationFrame(draw);
  });
};

/* Helpers */

function changeSelection(symbol) {
  selectionState.changeState(symbol);

  uiJs.showControls(symbol !== null);

  if (symbol !== null) {
    const props = mandalaState.getPrimitiveState(symbol).props;

    parameters.forEach(parameter => {
      uiJs.updateControlsFor(parameter.name, props[parameter.name])
    });
    uiJs.updateFlipToggle(props.flip);
  }

  requestAnimationFrame(draw);
}